import React from 'react';
import { Link } from 'react-router-dom';
// reactstrap components
import {
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  DropdownToggle,
  Form,
  FormGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  InputGroup,
  Navbar,
  Nav,
  Container,
  Media
} from 'reactstrap';

import { getUsername } from 'utils';

import Parse from 'parse';
import { getCurrentUser } from 'utils';
import { getFullname } from 'utils';

class AdminNavbar extends React.Component {
  handleLogout = (e) => {
    e.preventDefault();
    let confirmx = window.confirm('Anda akan logout dari sistem');
    if (confirmx) {
      Parse.User.logOut().then(() => {
        localStorage.removeItem('roles');
        return this.props.history.push('/auth/login');
      });
    } else {
      return;
    }
  };

  render() {
    return (
      <React.Fragment>
        <Navbar className="navbar-top navbar-dark" expand="md" id="navbar-main">
          <Container fluid>
            <Link className="h4 mb-0 text-white text-uppercase d-none d-lg-inline-block" to="/">
              {this.props.brandText}
            </Link>
            <Form className="navbar-search navbar-search-dark form-inline mr-3 d-none d-md-flex ml-lg-auto">
              <FormGroup className="mb-0">
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="fas fa-search" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input placeholder="Search" type="text" />
                </InputGroup>
              </FormGroup>
            </Form>
            <Nav className="align-items-center d-none d-md-flex" navbar>
              <UncontrolledDropdown nav>
                <DropdownToggle className="pr-0" nav>
                  <Media className="align-items-center">
                    <span className="avatar avatar-sm rounded-circle">
                      <img
                        alt="..."
                        width={37}
                        height={37}
                        src={getCurrentUser().get('fotoWajah').url()}
                      />
                    </span>
                    <Media className="ml-2 d-none d-lg-block">
                      <span className="mb-0 text-sm font-weight-bold">
                        {/* {getUsername() ? getUsername() : ''} */}
                        {getFullname() ? getFullname() : ''}
                      </span>
                    </Media>
                  </Media>
                </DropdownToggle>
                <DropdownMenu className="dropdown-menu-arrow" right>
                  <DropdownItem className="noti-title" header tag="div">
                    <h6 className="text-overflow m-0">Welcome!</h6>
                  </DropdownItem>
                  {window.localStorage.getItem('roles') === 'admin' ? (
                    <DropdownItem to="/admin/user-profile" tag={Link}>
                      <i className="ni ni-settings-gear-65" />
                      <span>Settings</span>
                    </DropdownItem>
                  ) : (
                    ''
                  )}
                  <DropdownItem divider />
                  <DropdownItem href="#pablo" onClick={this.handleLogout}>
                    <i className="ni ni-user-run" />
                    <span>Logout</span>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Container>
        </Navbar>
      </React.Fragment>
    );
  }
}

export default AdminNavbar;
